.card{background-color:#fff;border-radius:1rem;padding:1rem;max-width:20rem;width:100%;box-shadow:0 4px 6px rgba(0,0,0,.1);border:1px solid #e2e8f0}.header{display:flex;align-items:center;gap:.75rem;margin-bottom:.5rem}.logo{width:2rem;height:2rem}.title{font-size:1.25rem;font-weight:bold}.details{color:#374151;font-size:.875rem;display:flex;flex-direction:column;gap:.25rem}.error{color:red}